@media screen and (max-width: 1275px) {
  .location-details-wrapper {
    flex: 1;
  }
  .location-amenities {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 750px) {
  .amenity-categories {
    flex-wrap: wrap;
  }
  .amenity-header {
    min-width: 100%;
  }
  .location-header {
    flex-wrap: wrap;
  }
  .location-details-wrapper {
    min-width: 100%;
  }
}
@media screen and (max-width: 450px) {
  .amenities {
    font-size: var(--font-size-lgi);
  }
  .amenities-list {
    gap: var(--gap-mini);
  }
  .where-youll-be {
    font-size: var(--font-size-lgi);
  }
