.stays-nearby,
.toronto-ontario {
  margin: 0;
  position: relative;
  font-size: inherit;
  letter-spacing: 0.02em;
  line-height: 120%;
  font-family: inherit;
}
.stays-nearby {
  font-weight: 400;
  display: inline-block;
  min-width: 127px;
}
.toronto-ontario {
  font-weight: 600;
}
.nearby-stays {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-9xs);
}
.bento-menu-1-icon {
  height: 20px;
  width: 20px;
  position: relative;
}
.bento-menu {
  border-radius: var(--br-7xs);
  background-color: var(--accent);
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-5xs);
}
.map-icon {
  height: 20px;
  width: 20px;
  position: relative;
}
.bento-menu1 {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-5xs);
}
.layout-selection,
.search-section-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  box-sizing: border-box;
}
.layout-selection {
  width: 84px;
  border-radius: var(--br-4xs);
  background-color: var(--white);
  border: 0.7px solid var(--light-grey-border);
  justify-content: flex-start;
  padding: var(--padding-9xs) var(--padding-8xs);
}
.search-section-header {
  width: 100%;
  justify-content: space-between;
  padding: var(--padding-sm) 0 0;
  max-width: 1280px;
  gap: var(--gap-xl);
  text-align: left;
  font-size: var(--font-size-xl);
  color: var(--color-black);
  font-family: var(--font-archivo);
}
@media screen and (max-width: 1275px) {
  .search-section-header {
    max-width: 100%;
  }
}
@media screen and (max-width: 750px) {
  .search-section-header {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 450px) {
  .stays-nearby,
  .toronto-ontario {
    font-size: var(--font-size-base);
    line-height: 19px;
  }
}
